@import 'blocks/header';
@import 'blocks/card';
@import 'blocks/stars';

html {
  --card-color: #fff;
  --main-color-button: #00acdc;
  --text-accent-color: #060b35;
  --text-secondary-color: #616070;

  font-family: Roboto, sans-serif;
  font-size: 10px;
  line-height: 14px;
}

body {
  margin: 0;
  display: block;
  padding: 0;
  box-sizing: border-box;
}
